.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  color: white;
  background-image: linear-gradient(79deg, #7600bb, #c66fbc 48%, #f7944d );

}

.auth-form-container, .login-form, .register-form {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 600px) {
  .auth-form-container {
    padding: 5rem;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0.5rem;
  }

}

label {
  text-align: left;
  padding: 0.3rem 0;
}

input {
margin: 0.5rem 0;
padding: 1rem;
border: none;
border-radius: 10px;
}

button{
  border: none;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  color: #7600bb;
}

.redirect-button {
  background: none;
  color: white;
  text-decoration: underline;
}